<template>
	<!-- 防止闪烁 -->
	<view v-if="product.name">
		<view class="swiper-wraper">
			<swiper :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000" :circular="true">
				<swiper-item v-for="item in product.skuList" :key="item.id"><image :src="item.pic" mode="widthFix"></image></swiper-item>
			</swiper>
			<view class="collect" @click="collectClick(product)">
				<view class="bgc">
					<uni-icons type="star" size="20" color="#fff" v-if="!isCollected"></uni-icons>
					<uni-icons type="star-filled" size="20" color="#f00" v-else></uni-icons>
				</view>
			</view>
		</view>
		<view class="dec">
			<view class="row1">
				<text class="nowPrice">￥{{ product.price }}</text>
				<text class="desPrice">￥{{ product.price }}</text>
				<text class="cuxiao">没有促销使用原价</text>
			</view>
			<view class="row2">
				<text>{{ product.name }}</text>
			</view>
			<view class="row3">
				<text>{{ product.description }}</text>
			</view>
		</view>
		<mp-html :tag-style="obj" :content="product.detailMobileHtml" />
	</view>
</template>

<script>
import { productDetail } from '@/api/category/index.js';
export default {
	data() {
		return {
			productId: '',
			product: {},
			isCollected: false,
			obj: {
				// #ifdef H5
				img: 'width:80%;height:100%;margin:0 auto;display:block;',
				// #endif
				// #ifdef MP-WEIXIN
				img: 'width:80%;margin:0 auto;display:block;'
				// #endif
			}
		};
	},
	async onLoad(options) {
		console.log(options);
		this.productId = options.productId;
		const { data: res } = await productDetail(this.productId);
		if (!res.success) return uni.$showMsg();
		// 修补数字位数
		res.data.product.price = res.data.product.price.toFixed(2);
		// 添加一个收藏的属性 默认为false
		res.data.product.isCollected = false;
		// console.log(res.data.product.price.toFixed(2));
		this.product = res.data.product;
		uni.setNavigationBarTitle({
			title: res.data.product.name
		});
		// 收藏图标的设置
		const cList = uni.getStorageSync('collectList');
		if (cList) {
			const cIndex = cList.findIndex(el => el.id == res.data.product.id);
			console.log(cIndex);
			this.isCollected = cList[cIndex].isCollected;
		}
	},
	methods: {
		CollectedYN(item) {
			this.isCollected = item.isCollected;
			if (this.isCollected) {
				uni.$showMsg({ title: '收藏成功' });
			} else {
				uni.$showMsg({ title: '取消收藏' });
			}
		},
		collectClick(item) {
			// console.log(item);
			item.isCollected = !item.isCollected;
			const cList = uni.getStorageSync('collectList');
			const collectList = [...cList];
			if (cList) {
				// console.log(cList.findIndex(el => el.id == item.id));
				const cIndex = cList.findIndex(el => el.id == item.id);
				if (cIndex !== -1) {
					cList[cIndex].isCollected = !cList[cIndex].isCollected;
					this.CollectedYN(item);
				}
				if (cIndex === -1) {
					// console.log(item);
					// item.isCollected = !item.isCollected;
					collectList.push(item);
					this.CollectedYN(item);
				}
			} else {
				//不存在才添加
				collectList.push(item);
				this.CollectedYN(item);
			}
			uni.setStorageSync('collectList', collectList);
		}
	}
};
</script>

<style lang="scss" scoped>
.swiper-wraper {
	position: relative;
	.bgc {
		position: absolute;
		bottom: -40rpx;
		right: 56rpx;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		width: 96rpx;
		height: 96rpx;
		background-color: $navigateBackgroundColor;
	}
	swiper {
		height: 608rpx;
		swiper-item {
			image {
				width: 100%;
				height: 100%;
			}
		}
	}
}
.dec {
	padding: 40rpx;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	.row1 {
		text-align: center;
		.nowPrice {
			font-family: fantasy;
			font-size: 56rpx;
			color: #f2270c;
		}
		.desPrice {
			font-family: fantasy;
			color: #b5b5b5;
			font-size: 24rpx;
			text-decoration: line-through;
		}
		.cuxiao {
			background-color: #f2270c;
			color: #fff;
			width: 220rpx;
			height: 34rpx;
			font-size: 24rpx;
			padding: 10rpx;
			border-radius: 12rpx;
		}
	}
	.row2 {
		padding: 20rpx 6rpx;
		color: #262626;
		font-size: 15px;
		font-weight: 700;
	}
	.row3 {
		font-size: 11px;
		padding: 18px 0 0;
		line-height: 1.3;
		color: #666;
	}
	.row1,
	.row2,
	.row3 {
		display: flex;
		justify-content: space-around;
		align-items: center;
	}
}
</style>
